import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import VueSession from 'vue-session'

Vue.use(Router)
var sessionOptions = {
  persist: true
}

Vue.use(VueSession, sessionOptions)

const Main = () => {
	return import("./views/Main.vue")
}

const SCR001001List = () => {
	return import("./views/SCR001/SCR001001List.vue")	
}
const SCR001002List = () => {
	return import("./views/SCR001/SCR001002List.vue")	
}
const SCR001003List = () => {
	return import("./views/SCR001/SCR001003List.vue")	
}

const MOB001000 = () => {
  return import("./views/MOB001/MOB001000.vue")
}
const MOB001001 = () => {
  return import("./views/MOB001/MOB001001.vue")
}
const MOB001002 = () => {
  return import("./views/MOB001/MOB001002.vue")
}
const MOB001003 = () => {
  return import("./views/MOB001/MOB001003.vue")
}
const MOB001004 = () => {
  return import("./views/MOB001/MOB001004.vue")
}
const MOB001005 = () => {
  return import("./views/MOB001/MOB001005.vue")
}
const MOB001006 = () => {
  return import("./views/MOB001/MOB001006.vue")
}
const MOB001007 = () => {
  return import("./views/MOB001/MOB001007.vue")
}
const MOB001008 = () => {
  return import("./views/MOB001/MOB001008.vue")
}
const BarChart = () => {
  return import("./views/DBO001/BarChart.vue")
}
const PieChart = () => {
  return import("./views/DBO001/PieChart.vue")
}
const LineChart = () => {
  return import("./views/DBO001/LineChart.vue")
}
const AreaChart = () => {
  return import("./views/DBO001/AreaChart.vue")
}

export const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes : [
    {
      path: '/',
      name: 'main',
      component: Main,
    },

    {
      path: '/SCR001001List',
      name: 'scr001001List',
      component: SCR001001List
    },
    {
      path: '/SCR001002List',
      name: 'scr001002List',
      component: SCR001002List
    },
    {
      path: '/SCR001003List',
      name: 'scr001003List',
      component: SCR001003List
    },


    {
      path: '/MOB001000',
      name: 'mob001000',
      component: MOB001000
    },
    {
      path: '/MOB001001',
      name: 'mob001001',
      component: MOB001001
    },
    {
      path: '/MOB001002',
      name: 'mob001002',
      component: MOB001002
    },
    {
      path: '/MOB001003',
      name: 'mob001003',
      component: MOB001003
    },
    {
      path: '/MOB001004',
      name: 'mob001004',
      component: MOB001004
    },
    {
      path: '/MOB001005',
      name: 'mob001005',
      component: MOB001005
    },
    {
      path: '/MOB001006',
      name: 'mob001006',
      component: MOB001006
    },
    {
      path: '/MOB001007',
      name: 'mob001007',
      component: MOB001007
    },
    {
      path: '/MOB001008',
      name: 'mob001008',
      component: MOB001008
    },
    {
      path: '/BarChart',
      name: 'barchart',
      component: BarChart
    },
    {
      path: '/PieChart',
      name: 'piechart',
      component: PieChart
    },
    {
      path: '/LineChart',
      name: 'linechart',
      component: LineChart
    },
    {
      path: '/AreaChart',
      name: 'areachart',
      component: AreaChart
    },
  ]
});

router.beforeEach(function (to, from, next) {
  console.log('변경하려는 경로 : ' + to.name);
  console.log('이전 경로 : ' + from.name);
  
  var authority_yn = 'N';
  var select_yn = 'N';
  var update_yn = 'N';
  var delete_yn = 'N';
  var screen_name = '';
  var from_location = from.name;
  var to_location = to.name;
  if(to.name == 'main' || to.name == 'mob001000'|| to.name == 'mob002000'){
    to.query.authority_yn = 'Y';
    to.query.select_yn = 'Y';
    to.query.update_yn = 'Y';
    to.query.delete_yn = 'Y';
    to.query.screen_name = '';
    to.query.from_location = from_location;
    to.query.to_location = to_location;
    next({ query: to.query })
  }else{
    if(to.name == 'mob001001' || to.name == 'mob001002' || to.name == 'mob001003' || to.name == 'mob001004' || to.name == 'mob001005' || to.name == 'mob001006' || to.name == 'mob001007' || to.name == 'mob001008' || to.name == 'mob002002' || to.name == 'mob002003' || to.name == 'mob002004' || to.name == 'mob002005'){
      if( typeof router.app.$session.get('user_id') =='undefined'){
        if( to.name == 'mob001001' || to.name == 'mob001002' || to.name == 'mob001003' || to.name == 'mob001004' || to.name == 'mob001005' || to.name == 'mob001006' || to.name == 'mob001007' || to.name == 'mob001008'){
          next('mob001000');
          // next(from.path);
        }else if(to.name == 'mob002002' || to.name == 'mob002003' || to.name == 'mob002004' || to.name == 'mob002005'){
          next('mob002000');
        }
        return
      }
    }
    if(router.app.$session.get('user_authority') != '0001'){
      axios.get('/back/Common/Authority',{
        params: {
            user_id: router.app.$session.get('user_id'),
            to: to.name 
          }
        })
        .then(response => {
          authority_yn = response.data.clm_authority_yn;
          select_yn = response.data.clm_select_yn;
          update_yn = response.data.clm_update_yn;
          delete_yn = response.data.clm_delete_yn;
          screen_name = response.data.clm_screen_name;
          
          if(select_yn == 'Y'){ // 권한 있음.
            to.query.authority_yn = authority_yn;
            to.query.select_yn = select_yn;
            to.query.update_yn = update_yn;
            to.query.delete_yn = delete_yn;
            to.query.screen_name = screen_name;
            to.query.from_location = from_location;
            to.query.to_location = to_location;
            
            next({ query: to.query })
          }else{
            alert('해당 화면의 권한이 없습니다.');
            if(from.name == null || to.name == null || from.name == 'main' || to.name == 'main'){
              if(to.name == 'mob001000' || to.name == 'mob001001' || to.name == 'mob001002' || to.name == 'mob001003' || to.name == 'mob001004' || to.name == 'mob001005' || to.name == 'mob001006' || to.name == 'mob001007' || to.name == 'mob001008'){
                next('mob001000');
              }else if(to.name == 'mob002000' ||to.name == 'mob002002' || to.name == 'mob002003' || to.name == 'mob002004' || to.name == 'mob002005'){
                next('mob002000');
              }else{
                next('/')
              }
            }else{
              if(from.query.authority_yn != null){
                to.query.authority_yn = from.query.authority_yn;
                to.query.select_yn = from.query.select_yn;
                to.query.update_yn = from.query.update_yn;
                to.query.delete_yn = from.query.delete_yn;
                to.query.screen_name = from.query.screen_name;
                to.query.from_location = from_location;
                to.query.to_location = to_location;
              }
              // next('/')
              if(to.name == 'mob001000' || to.name == 'mob001001' || to.name == 'mob001002' || to.name == 'mob001003' || to.name == 'mob001004' || to.name == 'mob001005' || to.name == 'mob001006' || to.name == 'mob001007' || to.name == 'mob001008'){
                next('mob001000');
              }else if(to.name == 'mob002000' ||to.name == 'mob002002' || to.name == 'mob002003' || to.name == 'mob002004' || to.name == 'mob002005'){
                next('mob002000');
              }else{
                next('/')
              }
              // next(from.path)
            }
          }
        });
    }else{
      // 0001 : 전체권한
      authority_yn = 'Y';
      select_yn = 'Y';
      update_yn = 'Y';
      delete_yn = 'Y';
      screen_name = '';
      
      axios.get('/back/Common/Authority',{
        params: {
            user_id: router.app.$session.get('user_id'),
            to: to.name 
          }
        })
        .then(response => {
          to.query.screen_name = response.data.clm_screen_name;
        });

      to.query.authority_yn = authority_yn;
      to.query.select_yn = select_yn;
      to.query.update_yn = update_yn;
      to.query.delete_yn = delete_yn;
      to.query.screen_name = screen_name;
      to.query.from_location = from_location;
      to.query.to_location = to_location;
      
      next({ query: to.query })

      //next()
    }
  }
});